@use '../util' as *;


.cards {
    display: grid;
    grid-template-columns: 1fr;
    gap: rem(30);

    @include breakpoint(medium) {
        grid-template-columns: repeat(2, 1fr);
    }

    @include breakpoint(large) {
        grid-template-columns: repeat(4, 1fr);
    }
}

.card {
    position: relative;
    overflow: hidden;
    background: var(--dark-card);
    color: var(--dark-text1);
    text-align: center;
    padding: rem(25);
    border-radius: rem(5);

    &--facebook {
        border-top: solid 4px var(--facebook);
    }
    &--twitter {
        border-top: solid 4px var(--twitter);
    }
    &--instagram {
        padding-top: rem(30) ;
        &::before {
            content: '';
            position: absolute;
            display: block;
            left: 0;
            top: 0;
            height: rem(4);
            width: 100%;
            background: linear-gradient(225deg, var(--instagram-end) 0%, var(--instagram-middle) 50%, var(--instagram-start));
        }
    }
    &--youtube {
        border-top: solid 4px var(--youtube);
    }

    &__platform {
        display: flex;
        justify-content: center;
        align-items: center;
        height: rem(20);
        margin-top: rem(5);
        margin-bottom: rem(28);
    }
    
    &__icon {
        margin-right: rem(8);

        &--facebook {

        }
        &--twitter {

        }
        &--instagram {

        }
        &--facebook {

        }
        &--youtube {

        }
    }

    &__username {
        font-size: rem(12);
        font-weight: 700;
    }

    &__followers {
        margin-bottom: rem(25);
    }

    &__count {
        color: var(--light-bg);
        font-weight: 700;
        letter-spacing: rem(-2);
        line-height: 1;
        margin-bottom: rem(4);

        &--big {
            font-size: rem(56);

        }
        &--small {

        }
    }

    &__label {
        font-size: rem(12);
        letter-spacing: rem(5);
        font-weight: 400;
        color: var(--dark-text1);
        text-transform: uppercase;
        
    }

    &__change {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: rem(12);
        font-weight: 700;

        &--up {
            color: var(--limegreen);
        }
        &--down {
            color: var(--brightred);
        }
        img {
            margin-right: rem(4);
        }
    }
    &__number {
        
    }
}
